<template>
  <div>
    <h2 class="m-3">Custom type list</h2>

    <table class="table">
      <col width="5%" />

      <thead>
        <tr>
          <th></th>
          <th>Name</th>
          <th>Id</th>
          <th>Order</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="customType in customTypes" :key="customType._id">
          <td class="text-muted">
            <button
              type="button"
              class="btn btn-link"
              v-on:click="deleteCustomType(customType._id)"
            >
              <i class="far fa-trash-alt"></i>
            </button>
          </td>
          <td>{{customType.name}}</td>
          <td>
            <router-link :to="{ path: customType._id}" append>{{customType._id}}</router-link>
          </td>
          <td>
            {{customType.order}}
          </td>
        </tr>
      </tbody>
    </table>

    <button v-on:click="createCustomType()" class="btn btn-light mt-3">Create</button>

  </div>
</template>

<script>
import axios from "axios";
// import NavigationPost from "./NavigationPost.vue";

export default {
  name: "custom-type",
  components: {
    // NavigationPost
  },
  data: function() {
    return {
      customTypes: []
    };
  },
  methods: {
    createCustomType: function() {
      axios
        .post("http://localhost:3000/API/customTypes/create", {})
        .then(response => {
          this.customTypes.push(response.data.post);
          this.$router.push('/admin/customTypes/' + response.data.post._id)
        });
    },
    deleteCustomType: function(postId) {
      axios
        .post("http://localhost:3000/API/customTypes/delete", {
          postId: postId
        })
        .then(() => {
          var indexToDelete = this.customTypes.findIndex(function(value) {
            return value._id == postId;
          });
          this.customTypes.splice(indexToDelete, 1);
        });
    }
  },
  mounted: function() {
    axios.get("http://localhost:3000/API/customTypes").then(response => {
      this.customTypes = response.data.customTypes;
    });
  }
};
</script>
