
<template>
  <div class="container py-3">
    <div class="row">
      <div class="col mb-9">
        <h1>{{post.title}}</h1>
        <div class="card">
          <div class="card-header">{{post.title}}</div>
          <div class="card-body">
            <h5 class="card-title">{{post.title}}</h5>
            <p class="card-text">{{post.content}}</p>
          </div>
          <div class="card-footer text-muted">{{post.createdAt}}</div>
        </div>
      </div>
      <div class="col mb-3">
        <div class="card">
          <div class="card-body">
            <div class="pb-5">
              <h5 class="card-title">Le {{post.createdAt}}</h5>
              <h5 class="card-title">Ecrit par {{post.authorId}}</h5>
            </div>
            <p v-show="post.categories" class="card-text">Cathgories {{post.categories}}</p>
            <p v-show="post.tags" class="card-text">Tags {{post.tags}}</p>
          </div>
          <div class="card-footer text-muted">{{post.createdAt}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "PostsDetails",
  components: {},
  data: function() {
    return {
      customTypeName: "",
      customType: {},
      post: {}
    };
  },
  mounted() {
    this.customTypeName = this.$route.params.customTypeName;
    var postId = this.$route.params.postId;
    axios
      .get("http://localhost:3000/API/" + this.customTypeName + "/" + postId)
      .then(response => {
        this.post = response.data.post;
      });

      // charge le type paramtr du post
      this.mountPostType()
  },
  methods: {
    mountPostType: function() {
      axios
        .get("http://localhost:3000/API/customTypes/name/" + this.customTypeName)
        .then(response => {
          this.customType = response.data.post;
        });
    }
  }
};
</script>
