<template>
  <div>
    <nav class="navbar navbar-expand-lg bg-light">
      <router-link to="/admin" class="navbar-brand">
        <img src="/img/icon.png" width="30" height="30" class="d-inline-block align-top" alt />
        OP Admin
      </router-link>

      <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
        <div class="navbar-nav mr-auto">
          <router-link
            v-for="menu in menus"
            :key="menu._id"
            :to="'/admin/' + menu.name"
            class="nav-item nav-link border-left px-3"
          >
            <i :class="menu.icon"></i>
            {{menu.name | capitalize}}
          </router-link>

          <router-link to="/admin/customTypes" class="nav-item nav-link border-left px-3">
            <i class="fab fa-suse"></i>
            Custom
          </router-link>
        </div>
        <form class="form-inline">
          <router-link to="/" class="btn btn-light" role="button">View site</router-link>
        </form>
      </div>
    </nav>
    <!-- Welcome {{user.name}} -->
  </div>
</template>

<script>
import axios from "axios";
// import NavigationPost from "./NavigationPost.vue";

export default {
  name: "admin-header",
  components: {
    // NavigationPost
  },
  data: function() {
    return {
      user: {},
      menus: []
    };
  },
  methods: {},
  mounted: function() {
    axios.get("http://localhost:3000/API/adminMenus").then(response => {
      this.menus = response.data.menus;
    });

    axios.get("http://localhost:3000/API/user").then(response => {
      this.user = response.data.user;
    });
  }
};
</script>
