<template>
  <div class="container my-5">
    <article v-for="post in posts" :key="post._id" class="mb-3">
      <header></header>

      <div class="card">
        <div class="card-body">
          <h5 class="card-title">
            <router-link :to="customTypeName + '/' + post._id">{{post.title || "-" }}</router-link>
          </h5>

          <p class="card-text">{{post.content}}</p>

          <p class="card-text">
            <small class="text-muted">{{post.createdAt}}</small>
          </p>

          <div class="d-flex justify-content-between">
            <router-link :to="'posts?categories='+ post.categories">{{post.categories}}</router-link>
            <router-link :to="'posts?tags='+ post.tags">{{post.tags}}</router-link>
          </div>
          <router-link :to="'posts?users='+ post.authorId">{{post.authorId}}</router-link>
        </div>
      </div>

      <footer></footer>
    </article>
  </div>
</template>

<script>
import axios from "axios";

// @ is an alias to /src

export default {
  name: "posts",
  components: {},
  data: function() {
    return {
      customTypeName: "",
      posts: []
    };
  },
  methods: {
    mountPosts: function() {
      this.customTypeName = this.$route.params.customTypeName;

      axios
        .get("http://localhost:3000/API/" + this.customTypeName)
        .then(response => {
          this.posts = response.data.posts;
        });
    }
  },
  mounted() {
    this.mountPosts();
  },
  watch: {
    $route() {
      this.mountPosts();
    }
  }
};
</script>
