
<template>
  <div class="container py-3">
    <h1 class="py-3">{{customTypeName | capitalize}}</h1>
    <div v-for="post in this.posts" :key="post._id" class="row row-cols-1 row-cols-md-2">
      <div class="col mb-4">
        <div class="card">
          <img :src="post.img" class="card-img-top" :alt="post.img" />
          <div class="card-body">
            <router-link :to="customTypeName + '/' + post._id">{{post.title || "-" }}</router-link>
            <p class="card-text">{{post.content}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

// @ is an alias to /src
// import MyHeader from "@/templates/MyHeader";

export default {
  name: "Page",
  components: {},
  data: function() {
    return {
      customTypeName: "pages",
      posts: []
    };
  },
  methods: {
    mountPosts: function() {
      // this.customTypeName = this.$route.params.customTypeName;

      axios
        .get("http://localhost:3000/API/" + this.customTypeName)
        .then(response => {
          this.posts = response.data.posts;
        });
    }
  },
  mounted() {
    this.mountPosts();
  },
  watch: {
    $route() {
      this.mountPosts();
    }
  }
};
</script>
