<template>
  <div>
    <div class="row">
      <div class="col-md-10">
        <h2 class="m-3">{{customType.name | capitalize }}</h2>
      </div>
      <div class="col-md-2">
        <button v-on:click="createPost()" class="btn btn-light mt-3">Create</button>
      </div>
    </div>

    <table class="table">
      <col width="5%" />

      <thead>
        <tr>
          <th></th>
          <th>Label</th>
          <th v-for="customTypeSetting in customTypeSettings" :key="customTypeSetting._id">
            <div v-if="customTypeSetting.hideTab == true"></div>
            <div v-else>{{customTypeSetting.name | capitalize}}</div>
          </th>
        </tr>
      </thead>

      <tbody>
        <!-- Une ligne pour chaque post -->
        <tr v-for="post in posts" :key="post._id">
          <!-- Colonne de suppression -->
          <td>
            <button type="button" class="btn btn-link" v-on:click="deletePost(post._id)">
              <i class="far fa-trash-alt"></i>
            </button>
          </td>

          <!-- Affichage d'une premire colonne pour le lien vers le post -->
          <td>
            <router-link :to="post._id" append>{{post[customTypeSettings[0].name] || "-"}}</router-link>
          </td>

          <!-- Une valeur pour chaque paramtres du type paramtr -->
          <td v-for="customTypeSetting in customTypeSettings" :key="customTypeSetting._id">
            <div v-if="customTypeSetting.hideTab == true"></div>
            <div v-else>{{post[customTypeSetting.name]}}</div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from "axios";

// @ is an alias to /src
// import MyHeader from "@/templates/MyHeader";

export default {
  components: {},
  data: function() {
    return {
      customType: {},
      customTypeSettings: [],
      posts: []
    };
  },
  methods: {
    createPost: function() {
      axios
        .post(
          "http://localhost:3000/API/" + this.customType.name + "/create",
          {}
        )
        .then(response => {
          this.posts.push(response.data.post);
          this.$router.push(
            "/admin/" + this.customType.name + "/" + response.data.post._id
          );
        });
    },
    deletePost: function(postId) {
      axios
        .post("http://localhost:3000/API/" + this.customType.name + "/delete", {
          postId: postId
        })
        .then(() => {
          var indexToDelete = this.posts.findIndex(function(value) {
            return value._id == postId;
          });
          this.posts.splice(indexToDelete, 1);
        });
    },
    mountType: function() {
      var customTypeName = this.$route.params.customTypeName;

      // Rcupration du type paramtr pour l'affichage dynamique des donnes
      axios
        .get("http://localhost:3000/API/customTypes/name/" + customTypeName)
        .then(response => {
          this.customType = response.data.post;
          this.customTypeSettings = this.customType.setting;
        });
    },
    mountPosts: function() {
      var customTypeName = this.$route.params.customTypeName;

      // Rcupration de tous les posts du type paramtr  afficher
      axios
        .get("http://localhost:3000/API/" + customTypeName)
        .then(response => {
          this.posts = response.data.posts;
        });
    }
  },
  mounted: function() {
    this.mountType();
    this.mountPosts();
  },
  watch: {
    $route() {
      // react to route changes...

      this.mountPosts();
      this.mountType();
    }
  }
};
</script>
