<template>
  <div class="form-group row">
    <label class="col-sm-2 col-form-label">{{customTypeSetting.name | capitalize}}</label>
    <div class="col-sm-10">
      <select v-model="post[customTypeSetting.name]" class="custom-select">
        <option
          v-for="targetPost in targetPosts"
          :key="targetPost.key"
          :value="targetPost['_id']"
        >{{targetPost[targetPostField]}}</option>
      </select>
      <small class="form-text text-muted">{{customTypeSetting.shemaDoc}}</small>
    </div>
  </div>
  <!-- targetPostType {{targetPostType}}
    <br />
    targetPostField {{targetPostField}}
    <br />
    targetPostId {{targetPostId}}
    <br />
    this.customTypeSetting.name {{customTypeSetting.name}}
    <br />
    this.post[this.customTypeSetting.name] {{post[customTypeSetting.name]}}
    <input
      type="text"
      class="form-control"
      v-model="post[customTypeSetting.name]"
  />-->
</template>



<script>
import axios from "axios";

export default {
  components: {},
  props: ["post", "customTypeSetting"],
  data: function() {
    return {
      options: this.customTypeSetting.options,
      targetPostType: {}, //this.customTypeSetting.postType,
      targetPostField: {}, //this.customTypeSetting.postField,
      targetPostId: {}, //this.post[customTypeSetting.name]
      targetPosts: []
    };
  },
  methods: {
    getPosts() {
      // Rcupration de tous les posts du type paramtr  afficher
      axios
        .get("http://localhost:3000/API/" + this.targetPostType)
        .then(response => {
          this.targetPosts = response.data.posts;
        });
    }
  },
  mounted() {
    this.targetPostType = this.customTypeSetting.postType;
    this.targetPostField = this.customTypeSetting.postField;
    this.targetPostId = this.post[this.customTypeSetting.name];
    this.getPosts();
  }
};
</script>

