<template>
  <div>
    <admin-header></admin-header>

    <div class="container">
      <router-view></router-view>
    </div>

    <footer class="text-center m-5">OP Admin by Olivier Perrier</footer>
  </div>
</template>

<script>
import AdminHeader from "./AdminHeader.vue";

import axios from "axios";

// @ is an alias to /src

export default {
  name: "app",
  components: {
    AdminHeader
  },
  data: function() {
    return {
      posts: String
    };
  },
  mounted() {
    axios.get("http://localhost:3000/API/posts").then(response => {
      this.posts = response.data.posts;
    });
  }
};
</script>
