<template>
  <div class="custom-type-detail">
    <h1>Custom type</h1>

    <navigation-post :post="post" :customType="customType"></navigation-post>

    <form class="my-3">
      <div class="form-group row">
        <label class="col-sm-2 col-form-label">Name</label>
        <div class="col-sm-10">
          <input type="text" class="form-control" v-model="post.name" />
          <small class="form-text text-muted">Name of the custom type</small>
        </div>
      </div>

      <div class="form-group row">
        <label class="col-sm-2 col-form-label">Setting</label>
        <div class="col-sm-10">
          <textarea type="text" class="form-control" rows="10" v-model="post.setting"></textarea>
          <small class="form-text text-muted">Define setting of the custom type</small>
        </div>
      </div>

      <div class="form-group row">
        <label class="col-sm-2 col-form-label">Icon</label>
        <div class="col-sm-10">
          <input type="text" class="form-control" v-model="post.icon" />
          <small class="form-text text-muted">Font awesome icon html</small>
        </div>
      </div>

      <div class="form-group row">
        <label class="col-sm-2 col-form-label">Order</label>
        <div class="col-sm-10">
          <input type="number" class="form-control" v-model="post.order" />
          <small class="form-text text-muted">Order for the menu</small>
          {{typeof this.post.order}}
        </div>
      </div>

      <!-- <div class="form-group row">
        <label class="col-sm-2 col-form-label">Template</label>
        <div class="col-sm-10">
          <input type="textarea" rows="10" class="form-control" v-model="post.template" />
          <small class="form-text text-muted">Custom template</small>
        </div>
      </div> -->
    </form>
  </div>
</template>

<script>
import axios from "axios";
import NavigationPost from "./NavigationPost.vue";

export default {
  name: "custom-type-detail",
  components: {
    NavigationPost
  },
  data: function() {
    return {
      post: {},
      customType: { name: "customTypes" },
      customTypeSettings: []
    };
  },
  mounted: function() {
    var postId = this.$route.params.id;

    axios
      .get("http://localhost:3000/API/customTypes/" + postId)
      .then(response => {
        this.post = response.data.post;
      });
  }
};
</script>
