<template>
  <div>
    <h2 class="m-3">Edit {{customType.name | capitalize}}</h2>

    <navigation-post :post="post" :customType="customType"></navigation-post>

    <!-- Affiche tous les champs paramtrs -->
    <div class="p-3">
      <form>
        <div v-for="customTypeSetting in customTypeSettings" :key="customTypeSetting._id">
          <component
            :is="'Field' + (customTypeSetting.type || 'String')"
            :post="post"
            :customTypeSetting="customTypeSetting"
          ></component>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";

import NavigationPost from "./NavigationPost.vue";

import FieldString from "./fields/FieldString.vue";
import FieldTextarea from "./fields/FieldTextarea.vue";
import FieldSelect from "./fields/FieldSelect.vue";
import FieldDate from "./fields/FieldDate.vue";
import FieldPassword from "./fields/FieldPassword.vue";
import FieldRelationship from "./fields/FieldRelationship.vue";

export default {
  components: {
    NavigationPost,
    FieldString,
    FieldTextarea,
    FieldSelect,
    FieldDate,
    FieldPassword,
    FieldRelationship
  },
  data: function() {
    return {
      customType: {},
      customTypeSettings: [],
      post: {}
    };
  },
  methods: {},
  mounted: function() {
    var customTypeName = this.$route.params.customTypeName;
    axios
      .get("http://localhost:3000/API/customTypes/name/" + customTypeName)
      .then(response => {
        this.customType = response.data.post;
        this.customTypeSettings = this.customType.setting;
      });

    var postId = this.$route.params.id;
    axios
      .get("http://localhost:3000/API/" + customTypeName + "/" + postId)
      .then(response => {
        this.post = response.data.post;
      });
  }
};
</script>
