import Vue from 'vue'
import Router from 'vue-router'


import App from './templates/App.vue'
import Home from './templates/Home.vue'
import Posts from './templates/Posts.vue'
import PostsDetails from './templates/PostsDetails.vue'
import Pages from './templates/Pages.vue'
import Users from './templates/Users.vue'

// var test = "blog/"

// var TemplateUsers = require("./templates/" + test + "Users.vue");
// var TemplatePosts = require("./templates/" + test + "Posts.vue");

// console.log(TemplateUsers)
// console.log(TemplatePosts)


import AppAdmin from './components/admin/App.vue'
import CustomTypeDetail from './components/admin/CustomTypeDetail.vue'
import CustomType from './components/admin/CustomType.vue'
import AdminPosts from './components/admin/AdminPosts.vue'
import AdminPostsEdit from './components/admin/AdminPostsEdit.vue'

import Login from './components/admin/Login.vue'
import Logout from './components/admin/Logout.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/admin',
      component: AppAdmin,
      children: [
        {
          path: 'customTypes/:id',
          component: CustomTypeDetail
        },
        {
          path: 'customTypes',
          component: CustomType
        },
        {
          path: ':customTypeName',
          component: AdminPosts
        },
        {
          path: ':customTypeName/:id',
          component: AdminPostsEdit
        }
      ]
    }, {
      path: '/',
      component: App,
      children: [
        {
          path: '/',
          component: Home
        }, {
          path: 'pages',
          component: Pages
        }, {
          path: 'users',
          component: Users
        }, {
          path: 'login',
          component: Login
        }, {
          path: 'logout',
          component: Logout
        }, {
          path: ':customTypeName/:postId',
          component: PostsDetails
        }, {
          path: ':customTypeName',
          component: Posts
        },
      ],
    }, {
      path: '/admin',
      name: 'admin-posts',
      components: { default: AdminPosts, admin: AdminPosts }
    }]
})
