<template>
  <div class="navigation-post">
    <div class="border rounded mt-3 p-1 d-flex justify-content-between">
      <router-link :to="'/admin/' + customType.name" class="btn btn-light">Return</router-link>

      <div>
        <button class="btn btn-success mx-3" type="button" v-on:click="save">Save</button>
        <!-- <a href="/{{model.name}}/{{post._id}}" class="btn btn-primary mr-3">View</a> -->
        <button v-on:click="deletePost" class="btn btn-danger btn-sm">Delete</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

// @ is an alias to /src
// import MyHeader from "@/templates/MyHeader";

export default {
  name: "NavigationPost",
  components: {},
  props: ["post", "customType"],
  methods: {
    save: function() {
      axios
        .post("http://localhost:3000/API/" + this.customType.name + "/save", {
          postId: this.post._id,
          post: this.post
        })
        .then(response => {
          response.data.num;
          this.$router.push("/admin/" + this.customType.name);
        });
    },
    deletePost: function() {
      axios
        .post("http://localhost:3000/API/" + this.customType.name + "/delete", {
          postId: this.post._id
        })
        .then(response => {
          this.$router.push("/admin/" + this.customType.name);
          response.data.num;
        });
    }
  }
};
</script>
