<template>
  <header>
    <nav class="navbar navbar-expand-lg navbar-light bg-light">
      <router-link to="/" class="navbar-brand">
        <img src="/img/icon.png" width="30" height="30" alt />
      </router-link>

      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarNav"
        aria-controls="navbarNav"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarNav">
        <ul class="navbar-nav">
          <li v-for="menu in menus" :key="menu.id" class="nav-item">
            <a v-if="menu.type == 'direct'" :href="menu.link" class="nav-link">{{menu.title}}</a>
            <router-link
              v-else-if="menu.type == 'posts' || menu.type == 'post'"
              :to="'/' + menu.link"
              class="nav-link"
            >{{menu.title | capitalize}}</router-link>
            <router-link
              v-else-if="menu.type == 'post'"
              :to="'/' + menu.link"
              class="nav-link"
            >{{menu.title}}</router-link>
          </li>
        </ul>

        <div class="navbar-nav border rounded text-right pr-1">
          <!-- Login Logout -->
          <router-link v-if="currentUser" to="/logout" class="nav-item nav-link">Logout</router-link>
          <router-link v-else to="/login" class="nav-item nav-link">Login</router-link>
          <!-- Admin -->
          <router-link to="/admin" class="nav-item nav-link">Admin</router-link>
          <!-- Current user -->
          <span v-if="currentUser" class="nav-item nav-link">{{currentUser.firstname}}</span>
        </div>
      </div>
    </nav>
  </header>
</template>

<script>
import axios from "axios";

export default {
  name: "MyHeader",
  data: function() {
    return {
      menus: [],
      currentUser: {}
    };
  },
  mounted() {
    axios.get("http://localhost:3000/API/menus").then(response => {
      this.menus = response.data.posts;
    });

    axios.get("http://localhost:3000/API/currentUser").then(response => {
      this.currentUser = response.data.currentUser;
    });
  },
  watch: {
    $route() {
      axios.get("http://localhost:3000/API/currentUser").then(response => {
        this.currentUser = response.data.currentUser;
      });
    }
  }
};
</script>

